body {
  width: 310px;
  margin: 0px;
  background-color: #535b78;

  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
a {
  display: block;
}
/*ðŸ‘‰**********App container**********ðŸ‘ˆ*/
.app--container {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-left: 40vw;
  margin-top: 40px;
  background-color: #313441;
  width: 310px;
  border-radius: 10px;
  color: white;
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.75);
}
/*ðŸ‘‰**********Info container**********ðŸ‘ˆ*/
.info--container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.info--container > img {
  width: 310px;
  border-radius: 10px 10px 0px 0px;
}
.info--container > h1 {
  margin: 10px;
}
.info--container > h4 {
  margin: 0px;
}
.info--container-buttons {
  width: 250px;
  display: flex;
  justify-content: space-between;
  margin-top: -10px;
}
.info--container-buttons > a {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  margin: 0px;
  width: 115px;
  height: 35px;
  border-radius: 10px;
  border: none;
  text-decoration: none;
}
.info--container-buttons > a:hover {
  box-shadow: 0px 0px 29px -6px rgba(230, 219, 219, 0.75);
  -webkit-box-shadow: 0px 0px 29px -6px rgba(230, 219, 219, 0.75);
  -moz-box-shadow: 0px 0px 29px -6px rgba(230, 219, 219, 0.75);
}
.info--container-buttons > a:active {
  box-shadow: 0px 0px 91px 20px rgba(230, 219, 219, 0.75);
  -webkit-box-shadow: 0px 0px 91px 20px rgba(230, 219, 219, 0.75);
  -moz-box-shadow: 0px 0px 91px 20px rgba(230, 219, 219, 0.75);
}

.info--button-email {
  background-color: white;
}
.info--button-linkedin {
  background-color: #5093e2;
  color: white;
}
/*ðŸ‘‰**********About container**********ðŸ‘ˆ*/
.about--container {
  margin: -5px 15px;
  font-family: "Inter", sans-serif;
}
.about--container > p {
  margin-top: -10px;
  font-size: 14px;
}

/*ðŸ‘‰**********Interests container**********ðŸ‘ˆ*/
.interests--container {
  margin: -5px 15px;
  font-family: "Inter", sans-serif;
}
.interests--container > p {
  margin-top: -10px;
  font-size: 14px;
}
/*ðŸ‘‰**********Footer container**********ðŸ‘ˆ*/
.footer--container {
  width: 310px;
  height: 50px;
  display: flex;
  gap: 26px;
  align-items: center;
  justify-content: center;
  margin-top: 15px;
  margin-bottom: 0px;
  background-color: #29292d;
  border-radius: 0px 0px 10px 10px;
  box-shadow: 0px -6px 5px -2px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: 0px -6px 5px -2px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px -6px 5px -2px rgba(0, 0, 0, 0.75);
}
.footer--container > a > img:hover {
  box-shadow: 0px 0px 29px -6px rgba(230, 219, 219, 0.75);
  -webkit-box-shadow: 0px 0px 29px -6px rgba(230, 219, 219, 0.75);
  -moz-box-shadow: 0px 0px 29px -6px rgba(230, 219, 219, 0.75);
  width: 30px;
}

.footer--container > a:active {
  box-shadow: 0px 0px 91px 20px rgba(230, 219, 219, 0.75);
  -webkit-box-shadow: 0px 0px 91px 20px rgba(230, 219, 219, 0.75);
  -moz-box-shadow: 0px 0px 91px 20px rgba(230, 219, 219, 0.75);
}
